import React, { Component } from 'react';
import { deleteList, fetchLists } from '../actions/listActions';
import { connect } from 'react-redux';
import PropTypes from 'prop-types'

class Lists extends Component {

  constructor(props){
    super(props);
    this.removeList = this.removeList.bind(this);
  }
  removeList(e, listId){
    if(this.props.token){
      this.props.deleteList('/listr_api/lists/', listId, this.props.token);
    }
  }

  render () {
    var hideRemove;
    const cardStyle = {
      display: 'inline-block',
      marginTop: '10px',
      marginRight: '10px'
    }
    var removeStyle = {
      display: 'inline',
    }
    if(this.props.listType === 'shared'){
      console.log("SHARED");
      hideRemove = {
        display: 'none',
      }
    }
    const data = this.props.data.map(list => (
      <div class="card" style={cardStyle} key={list.static_id}>
        <div class="card-header">
          <h4 class="card-title" style={removeStyle} >{list.name}</h4>
          <a class="btn" style={hideRemove} onClick={e => this.removeList(e, list.static_id)}><i class="fa fa-times"></i></a>
        </div>
        <div class="card-body">
          <h5 class="card-title mb-2 text-muted">{list.owner}</h5>
        </div>
      </div>
    ));
    return(
      <div>
        {data}
      </div>
    );
  }
}

Lists.proptypes = {
  deleteList: PropTypes.func.isRequired,
  fetchLists: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
});

export default connect(mapStateToProps, { deleteList, fetchLists })(Lists);



